/** @module CasterFooterButton
 *  @since 2023.03.01, 22:00
 *  @changed 2023.03.01, 22:00
 */

import { Box, Stack, SvgIconProps } from '@mui/material';
import classnames from 'classnames';

import styles from './CasterFooterButton.module.scss';

type TMuiIcon = React.ComponentType<SvgIconProps>;

type TIconTheme = 'alert';

interface TProps {
  className?: string;
  text?: string;
  icon?: TMuiIcon;
  crossed?: boolean;
  active?: boolean;
  badgeNumber?: number;
  theme?: TIconTheme;
}

export function CasterFooterButton(props: TProps) {
  const { className, text, icon, crossed, active, badgeNumber, theme } = props;
  const hasBadge = badgeNumber != null && !!badgeNumber;
  const hasIcon = !!icon;
  const Icon = hasIcon && icon;
  const component = active ? 'a' : 'span';
  const containerClassName = classnames(
    className,
    styles.container,
    active && styles.active,
    crossed && styles.crossed,
    hasIcon && styles.hasIcon,
    theme && styles['theme-' + theme],
  );
  // prettier-ignore
  return (
    <Stack className={containerClassName} flexDirection="row" alignItems="center" component={component}>
      <Box className={styles.background} />
      {!!Icon && (
        <Stack className={styles.icon} flexDirection="row" alignItems="center">
          <Icon className={styles.iconSymbol} />
          {crossed && <Box className={styles.crossedIcon} />}
        </Stack>
      )}
      {!!text && (
        <Box className={styles.text}>
          {text}
        </Box>
      )}
      {hasBadge && (
        <Box className={styles.badge}>
          {badgeNumber}
        </Box>
      )}
    </Stack>
  );
}
